*{
 margin: 0;
 padding: 0;
 text-align: center;
 background: #bda5a5;
}
.main-section
{
  width: 100%;
  height: 100%;
}
.container
{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  height: 550px;
  width: 350px;
  border: 4px solid #fff;
  padding: 10px 15px;
  background: #383232;
  border-radius: 30px;
  box-shadow: 0px 0px 15px 10px #bda5a5;
}
.user-box
{
  margin: 25px auto;
  width: 280px;
  height: 500px;
  background: #292323;
  border-radius: 30px;
}
.userprofile
{
  height: 300px;
  width: 100%;
  object-fit: cover;
}
.profile-name
{
  padding: 10px;
  background: #292323;
  text-align: center;
}
.profile
{
  background: #292323;
  color: #bda5a5;
  text-transform: capitalize;
  font-weight: bold;
  font-size: 30px;
}
.skills
{
  background: #292323;
  color: #bda5a5;
  text-transform: capitalize;
  font-size: 20px;
  font-weight: bold;
}
.text-messsage
{
  background: #292323;
  color: #bda5a5;
  text-transform: lowercase;
  text-align: center;
  font-size: 18px;
}
.icons
{
  justify-content: space-between;
  display: flex;
}